import {
	mapState,
	mapMutations
} from 'vuex'
import mInput from '../../components/m-input.vue'

export default {
	components: {
		mInput
	},
	data() {
		return {
			providerList: [],
			hasProvider: false,
			account: '',
			password: '',
			positionTop: 0
		}
	},
	methods: {
		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		bindLogin() {
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			if (this.account.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 6 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			/**
			 * 下面简单模拟下服务端的处理
			 * 检测用户账号密码是否在已注册的用户列表中
			 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
			 */
			uni.request({
				url: 'http://' + getApp().globalData.urlStr + '/Login',
				method: 'GET',
				data: {
					userName: this.account,
					pwd: this.password
				},
				success: res => {
					if (res.data.code == 0) {
						this.$store.state.userName = this.account;
						uni.switchTab({
							url: '../main/main',								
						});
					} else {
						uni.navigateBack();
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
	},
	onReady() {
		this.initPosition();
	}
}
